html, body {
  height: 100%;
}
body{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

#Calculator {
  width: 300px;
  min-height: 405px;
  background-color: black;
  padding: .3rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
#screen {
  min-height: 90px;
  padding: .5rem;
  display: flex;
  flex-direction: column;
  font-family: "Digital", monospace;
}

#formula {
  flex-basis: 40%;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  overflow-wrap: anywhere;
  color: rgb(255, 123, 0);
  font-size: 1.5rem;
}

#display {
  flex-basis: 60%;
  display: flex;
  align-items: center;
  overflow-wrap: anywhere;
  justify-content: flex-end;
  color: #fff;
  font-size: 2.3rem;
}

.gridContainer {
  display: grid;
  height: 300px;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(5, 1fr);
  gap: 1px;
  background-color: #000;
}
.gridChild {
  width: 100%;
  height: 100%;
  color: #fff;
  background-color: #555;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.3rem;
  font-weight: bold;
  cursor: pointer;
}
.gridChild:hover {
  border: 1px solid lightsteelblue;
  color: black;
  transform: scale(1.03);
}
.gridChild:active {
  border: 1px solid rgb(255, 123, 0);
}
.gridChild:focus {
  color: blue;
}
.gridChild:nth-child(1) {
  grid-row: 1/2;
  grid-column: 1/2;
  background-color: rgb(168, 7, 15);
}
.gridChild:nth-child(2) {
  background-color:rgb(168, 58, 7);
}
.gridChild:nth-child(3)
, .gridChild:nth-child(4)
, .gridChild:nth-child(8)
, .gridChild:nth-child(12) {
  background-color: #777;
}
.gridChild:nth-child(16) {
  background-color: rgb(13, 73, 128);
  grid-row: -1/-3;
  grid-column: -1/-2;
}
.gridChild:nth-child(17) {
  grid-row: -1/-2;
  grid-column: 1/3;
}